.borderRadius {
  -moz-border-radius: 5px;
  border-radius: 5px; }

.borderRadiusTp {
  -moz-border-radius: 5px 5px 0 0;
  border-radius: 5px 5px 0 0; }

.borderRadiusBtm {
  -moz-border-radius: 0 0 5px 5px;
  border-radius: 0 0 5px 5px; }

.ddcommon {
  position: relative;
  display: -moz-inline-stack;
  zoom: 1;
  display: inline-block;
  *display: inline;
  cursor: default;
  width: 50px !important;
  ul {
    padding: 0;
    margin: 0;
    li {
      list-style-type: none; } } }

.borderRadiusTp ul li:last-child {
  -moz-border-radius: 0 0 5px 5px;
  border-radius: 0 0 5px 5px;
  border-bottom: 0 none #c3c3c3; }

.borderRadiusBtm ul li:first-child {
  -moz-border-radius: 5px 5px 0 0;
  border-radius: 5px 5px 0 0;
  border-bottom: 1 solid #c3c3c3; }

.ddcommon {
  .disabled {
    img, span {
      opacity: .5;
      // standard: ff gt 1.5, opera, safari
      -ms-filter: "alpha(opacity=50)";
      // ie 8
      filter: alpha(opacity = 50);
      // ie lt 7
      -khtml-opacity: .5;
      // safari 1.x
      -moz-opacity: .5;
      // ff lt 1.5, netscape
      color: #999999; } }
  &.disabledAll {
    opacity: .5;
    // standard: ff gt 1.5, opera, safari
    -ms-filter: "alpha(opacity=50)";
    // ie 8
    filter: alpha(opacity = 50);
    // ie lt 7
    -khtml-opacity: .5;
    // safari 1.x
    -moz-opacity: .5;
    // ff lt 1.5, netscape
    color: #999999; }
  .clear {
    clear: both; }
  .shadow {
    -moz-box-shadow: 5px 5px 5px -5px #888888;
    -webkit-box-shadow: 5px 5px 5px -5px #888888;
    box-shadow: 5px 5px 5px -5px #888888; }
  .text {
    color: #7e7e7e;
    padding: 0;
    position: absolute;
    background: #fff;
    display: block;
    width: 98%;
    height: 98%;
    left: 2px;
    top: 0;
    border: none; } }

.ddOutOfVision {
  position: relative;
  display: -moz-inline-stack;
  display: inline-block;
  zoom: 1;
  *display: inline; }

.borderRadius .shadow {
  -moz-box-shadow: 5px 5px 5px -5px #888888;
  -webkit-box-shadow: 5px 5px 5px -5px #888888;
  box-shadow: 5px 5px 5px -5px #888888; }

.borderRadiusBtm .shadow {
  -moz-box-shadow: -5px -5px 5px -5px #888888;
  -webkit-box-shadow: -5px -5px 5px -5px #888888;
  box-shadow: -5px -5px 5px -5px #888888; }

.borderRadiusTp .border, .borderRadius .border {
  -moz-border-radius: 0 0 5px 5px;
  border-radius: 0 0 5px 5px; }

.borderRadiusBtm .border {
  -moz-border-radius: 5px 5px 0 0;
  border-radius: 5px 5px 0 0; }

img.fnone {
  float: none !important; }

.ddcommon {
  .divider {
    width: 0;
    height: 100%;
    position: absolute; }
  .arrow {
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 0;
    // &:hover
 }    //   background-position: 0 100%
  .ddTitle {
    padding: 0;
    position: relative;
    display: inline-block;
    width: 100%;
    .ddTitleText {
      display: block;
      .ddTitleText {
        padding: 0; } }
    .description {
      display: block; }
    .ddTitleText img {
      position: relative;
      vertical-align: middle;
      float: left; } }
  .ddChild {
    position: absolute;
    display: none;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    zoom: 1;
    li {
      clear: both;
      .description {
        display: block; }
      img {
        border: 0 none;
        position: relative;
        vertical-align: middle;
        float: left; }
      &.optgroup {
        padding: 0;
        .optgroupTitle {
          padding: 0 5px;
          font-weight: bold;
          font-style: italic; }
        ul li {
          padding: 5px 5px 5px 15px; } } } }
  .noBorderTop {
    border-top: none 0  !important;
    padding: 0;
    margin: 0; } }

