html,body {
  -webkit-text-size-adjust: 100%; }

html {
  min-height: 100%;
  height: 100%; }

body {
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  height: 100%;
  background-color: $color_white;
  overflow-x: hidden; }

main {
  flex-grow: 1; }

.btn-container {
  background-color: $bg_slider_btn_container;
  padding: 10px 15px;
  text-align: center; }

.btn {
  display: inline-block;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.2;
  text-transform: uppercase;
  text-decoration: none;
  color: $color_white;
  padding: 10px 10px;
  border-radius: 5px;
  transition: background 0.3s ease;
  text-align: center;

  &-donate {
    background-color: $btn_bg_color;
    &:hover {
      background-color: $color_light_blue; } }

  &-adopt {
    background-color: $color_white;
    color: $color_blue;
    width: 100%;
    font-size: 16px;
    &:hover {
      background-color: $color_light_blue;
      color: $color_white; } } }

button::-moz-focus-inner {
  border: 0; }

//default Drupal nav menu
.toolbar-loading #toolbar-administration {
  position: fixed;
  top: 0;
  z-index: 1500;
  left: 0;
  right: 0; }

.toolbar-horizontal .toolbar-tray {
  position: fixed !important; }

.block .content,
.node__content {
  margin-top: 0; }
